type TypeA = string
type TypeB = string | number | boolean
type User4 = {
    name : string
    age : number
    isValid : boolean
} | [string , number , boolean]

const userA: User4 = {
    name : 'Neo' ,
    age : 85 ,
    isValid : true
}

const userB : User4 = ['Evan' , 36 , false]

function someFunc(param : TypeB) : TypeA {
    switch (typeof param) {
        case "string":
            return param.toUpperCase()
        case "number" :
            return param.toFixed(2)
        default:
            return "Boolean!"
    }
}


